











import Vue from "vue";
import PropComponent from "@/components/Prop.vue";
import { Props } from "./Props";
import { Prop } from "./Prop";
import { Card } from "./Card/Card";

export default Vue.extend({
	model: {
		prop: "propData",
		event: "change"
	},
	props: {
		propData: Props
	},
	components: {
		Prop: PropComponent
	},
	watch: {
		propData: {
			deep: true,
			handler() {
				this.render();
			}
		}
	},
	methods: {
		render() {
			let propElement: HTMLDivElement = this.$el as HTMLDivElement;
		},
		emptyCheck() {
			if (this.propData.props.length == 0) {
				this.$socket.client.emit("game_propDelete", {
					roomName: this.getRoomName,
					prop: this.propData
				});
			}
		},
		pop() {
			let prop: Prop | undefined = this.propData.pop();
			if (prop) {
				this.$socket.client.emit("game_propCreate", {
					roomName: this.getRoomName,
					prop: prop
				});
				this.$emit("stateChange", false);
				this.emptyCheck();
			} else {
				this.emptyCheck();
			}
		},
		reverseAll() {
			this.propData.props.forEach(prop => {
				if (prop.componentName == "Card") {
					(prop as Card).reverse();
				}
			});
		},
		shuffle() {
			this.propData.shuffle();
			this.$emit("stateChange", false);
		},
		spreadAll() {
			this.propData.spread(this.propData.props.length).forEach(prop => {
				this.$socket.client.emit("game_propCreate", {
					roomName: this.getRoomName,
					prop: prop
				});
			});
			this.emptyCheck();
		}
	},
	computed: {
		getRoomName(): string {
			return this.$route.params.roomName;
		},
		getTopPropType(): string {
			let top: Prop | undefined = this.propData.getTop();
			if (top) return this.propData.getTop()!.componentName;
			else return "";
		}
	}
});
