




















import Vue from "vue";

interface RoomData {
	roomName: string;
	userCount: number;
}

export default Vue.extend({
	sockets: {
		game_getRooms(this: any, data: RoomData[]) {
			this.rooms = data;
		}
	},
	data() {
		return {
			rooms: [] as RoomData[],
			roomName: "" as string
		};
	},
	created() {
		this.$socket.client.emit("game_getRooms");
	},
	methods: {
		joinRoom(roomName: string) {
			this.$router.push({
				name: "room",
				params: {
					roomName: roomName
				}
			});
		}
	}
});
